<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    const validatePass = (rule, value, callback) => {
      const reg = !value.trim().match(/^[a-zA-Z]{1,}\d{1,}$/) || value.trim().length < 6;
      
      if (value === '') {
        callback();
      } else if (reg) {
        callback(new Error('请输入以字母开头的不少于六位的数字字母组合密码'));
      } else {
        if (this.formCustom.passwdCheck !== '') {
            // 对第二个密码框单独验证
            this.$refs.formCustom.validateField('passwdCheck');
        }
        callback();
      }
    };
    const validatePassCheck = (rule, value, callback) => {
      if (value.trim() !== this.formCustom.passwd.trim()) {
          callback(new Error('两次密码输入不同!'));
      } else {
          callback();
      }
    };
    
    return {
        formCustom: {
          admin: '',
          sort: '',
          passwd: '',
          passwdCheck: '',
          statisticalCode: '',
          radio: '',
          adminType: '',
        },
        ruleCustom: {
          admin: [
            { trigger: 'blur' }
          ],
          sort: [
            { trigger: 'change' }
          ],
          passwd: [
            { validator: validatePass, trigger: 'blur' }
          ],
          passwdCheck: [
            { validator: validatePassCheck, trigger: 'blur' }
          ],
          statisticalCode: [
            { trigger: 'blur' }
          ],
          radio: [
            { trigger: 'blur' }
          ],
          adminType: [
            { trigger: 'change' }
          ],
        },
        categoryList: [],
        groupList: []
    }
  },
  mounted: function() {
    const token = getToken(),
          uid = this.$route.query.uid,
          params = {"token": token, "uid": uid};

    api.adminDetail(params).then(res => {
      if(res.code == 0) {
        this.formCustom.admin = res.data.user.username;
        this.formCustom.sort = res.data.user.group_id;
        this.formCustom.radio = res.data.user.status;
        this.formCustom.adminType = res.data.user.category;
        this.categoryList = res.data.categoryList;
        this.groupList = res.data.groupList;
      } else {
        this.$Notice.error({
          title: res.msg
        })
      }
    })
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const params = {
            "token": getToken(),
            "adminid": this.$route.query.uid*1,
            "username": this.formCustom.admin,
            "newpasswd1": this.formCustom.passwd,
            "newpasswd2": this.formCustom.passwdCheck,
            "status": this.formCustom.radio*1,
            "adminGroup": this.formCustom.sort*1,
            "tjdm": this.formCustom.statisticalCode,
            "categoryList": this.formCustom.adminType*1
          };
          
          api.adminEditDetail(params).then(res => {
            if(res.code == 0) {
              this.$Notice.success({title:res.msg});
              this.$router.push({path: '/admin/adminlist'})
            } else {
              this.$Notice.error({title:res.msg});
            }
          }).catch(err => {
            this.$Notice.error({title:err});
          })
        } else {
          // this.$Notice.error({title: res.msg});
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    }
}
}
</script>
<template>
<div>
  <h3><Icon type="ios-bowtie" size=24 />修改管理员</h3>
  <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
    <FormItem label="账户" prop="admin">
        <Input v-model="formCustom.admin" placeholder="在此输入账户名"></Input>
    </FormItem>
    <FormItem label="账户分组" prop="sort">
      <Select v-model="formCustom.sort" placeholder="点击选择账户分组">
        <Option v-for="itm,index in groupList" :value="itm.id" :key="index">{{itm.title}}</Option>
      </Select>
    </FormItem>
    <FormItem label="密码" prop="passwd">
        <Input type="password" v-model="formCustom.passwd" placeholder="在此输入修改密码"></Input>
    </FormItem>
    <FormItem label="确认密码" prop="passwdCheck">
        <Input type="password" v-model="formCustom.passwdCheck" placeholder="在此输入确认密码"></Input>
    </FormItem>
      <FormItem label="统计代码" prop="statisticalCode">
        <Input v-model="formCustom.statisticalCode" show-word-limit type="textarea" :rows="8" placeholder="在此输入统计代码" style="width: 400px;" />
    </FormItem>
    <FormItem label="状态">
      <RadioGroup v-model="formCustom.radio">
          <Radio label="1">正常</Radio>
          <Radio label="2">停用</Radio>
      </RadioGroup>
    </FormItem>
    <FormItem label="账户类别" prop="adminType">
      <Select v-model="formCustom.adminType" placeholder="点击选择账户类别">
        <Option v-for="itm,index in categoryList" :value="index" :key="index">{{itm}}</Option>
      </Select>
    </FormItem>
    <FormItem>
        <Button type="primary" @click="handleSubmit('formCustom')">提交</Button>
        <Button @click="handleReset('formCustom')" style="margin-left: 8px">重置</Button>
    </FormItem>
  </Form>
</div>
    
</template>
<style lang="scss" scoped>
  .ivu-form {
		width: 28%;
	}
</style>